<template>
  <div class="home-m">
    <van-pull-refresh
      v-model="isRefreshLoading"
      :success-text="refreshSuccessText"
      :success-duration="1500"
      @refresh="onRefresh(page)"
    >
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad(page)"
      >
        <article-item
          v-for="(article, index) in articles"
          :key="index"
          :article="article"
        />
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArticles } from '@/api/article'
import ArticleItem from './components/article-item'
export default {
  name: 'HomeIndexM',
  components: {
    ArticleItem
  },
  props: {},
  data () {
    return {
      articles: [],
      loading: false,
      finished: false,
      timestamp: null,
      isRefreshLoading: false,
      refreshSuccessText: '',
      page: 1
    }
  },
  computed: {
  },
  watch: {
    user () {
    }
  },
  created () {
  },
  mounted () {},
  activated () {},
  methods: {
    async onLoad (page) {
      const { data } = await getArticles({
        page: page,
        per_page: 20
      })
      this.page++
      const { results } = data.data
      this.articles.push(...results)
      this.loading = false
      if (results.length) {
        this.timestamp = data.data.pre_timestamp
      } else {
        this.finished = true
      }
    },

    async onRefresh (page) {
      const { data } = await getArticles({
        page: page,
        per_page: 20
      })
      this.page++
      const { results } = data.data
      this.articles.unshift(...results)
      this.isRefreshLoading = false

      this.refreshSuccessText = `更新了${results.length}条数据`
    }
  }
}
</script>

<style scoped lang="less">
.home-m{
  position: fixed;
  left: 0;
  right: 0;
  bottom: 50px;
  top: 50px;
  overflow-y: auto;
}
</style>
